import React, { Component } from 'react';
import { useTranslation, withTranslation, Trans } from 'react-i18next';
import './Service.css';



class ServiceClass extends Component {
  render() {

    const { t, i18n } = this.props;

    return (
      <div className="Service">
        <div className="Service-container">
          <div className="Service-logoContainer">
            <img className="Service-logo" src={this.props.service.icon} alt={t(this.props.service.title)}></img>
            <p style={{ color: this.props.colorTitle }} className="Service-title">
              {t(this.props.service.title)}
              <img className="Service-title-marco" src={this.props.marcoServices} alt={t(this.props.service.title)}></img>
            </p>
          </div>
          <p style={{ color: this.props.color }} className="Service-description"> {t(this.props.service.description)}</p>
        </div>
      </div>
    );
  }
}

const Service = withTranslation()(ServiceClass);

export default Service;