import React, { Component } from 'react';
import { useTranslation, withTranslation, Trans } from 'react-i18next';
import './UnityDescription.css';



class UnityDescriptionClass extends Component {
  render() {

    const { t, i18n } = this.props;

    return (
      <div className="UnityDescription">
        <div className="UnityDescription-container">
          <img className="UnityDescription-puntos" src={this.props.unity.puntos} alt={this.props.unity.name}></img>
          <div className="UnityDescription-logoContainer">
            <img className="UnityDescription-logo" src={this.props.unity.logo} alt={this.props.unity.name}></img>
          </div>
          <p style={{color: this.props.unity.color}} className="UnityDescription-description"> {t(this.props.unity.description)}</p>
          <img className="UnityDescription-icon" src={this.props.unity.icon} alt={this.props.unity.name}></img>
        </div>
      </div>
    );
  }
}

const UnityDescription = withTranslation()(UnityDescriptionClass);

export default UnityDescription;