import React, { Component } from 'react';
import './Technology.css';



class Technology extends Component {
  render() {
    return (
      <div className="Technology">
        <div className="Technology-container">
          <div className="Technology-iconContainer">
            <img className="Technology-icon" src={this.props.technology.icon} alt={this.props.technology.title}></img>
            <p style={{ color: this.props.color }} className="Technology-title">
              {this.props.technology.title}
            </p>
          </div>
        </div>
      </div>
    );
  }
}

export default Technology;