import React, { Component } from 'react';
import './Event.css';



class Event extends Component {
  render() {
    return (
      <div className="Event">
        <div className="Event-container">
            <img className="Event-photo" src={this.props.event.photo} alt={this.props.event.title}></img>
            <img className="Event-photoTransparent" src={this.props.event.photoTransparent} alt={this.props.event.title}></img>
          <div className="Event-textContainer">
            <img className="Event-title-marco" src={this.props.marcoServices} alt={this.props.event.title}></img>
            <p style={{ color: this.props.color }} className="Event-title">
              {this.props.event.title}
            </p>
            <p style={{ color: this.props.event.colorSubtitle }} className="Event-subtitle"> ({this.props.event.subtitle})</p>
            <p style={{ color: this.props.color }} className="Event-description"> {this.props.event.description}</p>
          </div>
        </div>
      </div>
    );
  }
}

export default Event;